.products {
  padding-top: 149px;
  padding-bottom: 148px;
  background-color: $products-background-color;
  overflow: hidden;

  @media (max-width: $tablet-width-max) {
    padding-top: 50px;
    padding-bottom: 70px;
  }

  @media (max-width: $mobile-width-max) {
    padding-bottom: 57px;
  }
}

.products__container {
  width: 78%;

  @media (max-width: $tablet-width-max) {
    width: 590px;
  }

  @media (max-width: $mobile-width-max) {
    width: 270px;
  }
}

.products h2 {
  margin: 0;
  margin-bottom: 51px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: $features-title-color;
  text-align: center;

  @media (max-width: $tablet-width-max) {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 41px;
  }
}

.products__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 50px;

  @media (max-width: $tablet-width-max) {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  @media (max-width: $mobile-width-max) {
    grid-template-columns: auto;
    gap: 40px;
  }
}

.products__item {
  position: relative;

  &::before,
  &::after {
    position: absolute;
    content: "";
  }

  &--roadbike {
    padding-top: 10px;
    z-index: 10;

    &::after {
      width: 180px;
      height: 180px;
      top: 166px;
      left: -60px;
      border: 27px solid $button-color;
      border-radius: 50%;
      z-index: -1;

      @media (max-width: $tablet-width-max) {
        width: 127px;
        height: 127px;
        top: 211px;
        left: -37px;
        border: 18px solid $button-color;
      }

      @media (max-width: $mobile-width-max) {
        left: -25px;
      }
    }
  }

  &--mountainbike {
    padding-top: 39px;

    &::before,
    &::after {
      width: 47px;
      height: 47px;
      border: 8px solid $button-color;

      @media (max-width: $tablet-width-max) {
        display: none;
      }
    }

    &::before {
      top: 21px;
      left: -19px;
    }

    &::after {
      right: -23px;
      bottom: 32px;
    }

    @media (max-width: $tablet-width-max) {
      padding-top: 10px;
    }

    @media (max-width: $mobile-width-max) {
      padding-top: 1px;
    }
  }

  &--childbike {
    &::before {
      width: 95px;
      height: 95px;
      top: -32px;
      right: -36px;
      border-radius: 50%;
      border: 17px solid $button-color;

      @media (max-width: $tablet-width-max) {
        width: 68px;
        height: 68px;
        top: -21px;
        right: -25px;
        border: 13px solid $button-color;
      }

      @media (max-width: $mobile-width-max) {
        top: -19px;
        right: -14px;
      }
    }
  }
}

.products__item h3 {
  margin: 0;
  padding-top: 20px;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;
}

.products__item img {
  @media (max-width: $tablet-width-max) {
    width: 270px;
    height: 300px;
    border-radius: 10px;
  }
}
